.trails-main {
    position: absolute;
    width: 100%;
    height: 100%;
    margin-left: 30px;
    margin-top: 30px;
    overflow: hidden;
    cursor: pointer;
    display: flex;
    justify-content: left;
    align-items: left;
    text-align: left;
  }
  
  .trails-text {
    font-family: sans-serif;
    position: relative;
    width: 100%;
    height: 80px;
    line-height: 80px;
    color: white;
    font-size: 5em;
    font-weight: 800;
    text-transform: uppercase;
    text-align: left;
    will-change: transform, opacity;
    overflow: hidden;
    text-shadow: 1px 1px 5px whitesmoke;
  }
  
  .trails-text > div {
    overflow: hidden;
  }
  