$bg-color: #333;
$bl-color: #22BAD9;


.TypistExample {

    a {
      color: inherit;
      &:hover {
        background-color: $bl-color;
        color: white;
        text-decoration: none;
      }
    }
    &-header {
      margin-top: 5%;
      margin-bottom: 15%;
      text-align: center;
      font-size: 3em;
      cursor: pointer;
      color: $bl-color;
    }
    &-message {
      color: hotpink;
      width: 400px;
      margin: auto;
      .flash {
        color: $bl-color;
        font-weight: bold;
        text-decoration: underline;
  
        animation-name: blinker;
        animation-duration: 0.7s;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
        @keyframes blinker {
          0% { opacity: 1.0; }
          50% { opacity: 0.0; }
          100% { opacity: 1.0; }
        }
      }
    }
  }

  .Typist .Cursor {
    display: inline-block;
    color: white;
    font-size: 3em;
    
    &--blinking {
      opacity: 1;
      animation: blink 1s linear infinite;
      @keyframes blink {
        0% { opacity:1; }
        50% { opacity:0; }
        100% { opacity:1; }
      }
    }
  }
  